.AlertDialog_container {
  background-color: var(--profile_background);
  box-sizing: border-box;
  padding: 24px;
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 100%;
  width: 400px;
}

.AlertDialog_message {
  width: 100%;
  text-align: center;
  color: gray;
}

.AlertIcon_container {
  padding-bottom: 16px;
}

.AlertDialog_button_container {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-end;
  margin-top: 24px;
}

.AlertDialog_button {
  outline: none;
  text-decoration: none;
  border: none;
  border-radius: 8px;
  padding: 8px 16px;
  background-color: transparent;
  font-weight: bold;
  transition: all 0.3s;
  background-color: rgba(202, 213, 218, 0.308);
}

.AlertDialog_button:hover {
  background-color: rgba(161, 170, 179, 0.6);
  cursor: pointer;
}
