    right: 0;
    background-position: left;
  }
}

@keyframes slide {
  0% {
    left: 0;
    background-position: left;
  }

  25% {
    left: 50%;
  }

  50% {
    left: 0;
    right: 0;
    background-position: right;
  }

  75% {
    right: 50%;
  }

  100% {
    right: 0;
    background-position: left;
  }
}

