.Profile_toggle_fill {
  position: relative;
  width: var(--width);
  height: var(--height);
  border-radius: var(--border_radius);
  background: #dddddd;
  transition: background 0.2s;
}

.Profile_toggle_input:checked ~ .Profile_toggle_fill {
  background: #078;
}

.Profile_toggle_fill::after {
  content: "";
  position: absolute;
  top: var(--padding);
  bottom: var(--padding);
  left: var(--padding);
  height: calc(var(--height) - 2 * var(--padding));
  width: calc(var(--height) - 2 * var(--padding));
  background: white;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.25);
  border-radius: calc(var(--border_radius) - var(--padding));
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.Profile_toggle_input:checked ~ .Profile_toggle_fill::after {
  -webkit-transform: translateX(calc(var(--height)));
          transform: translateX(calc(var(--height)));
}

